<template>
    <div class="preloader">
        <div class="preloader-logo">the_mixcurb</div>
    </div>
</template>

<style>
    .preloader{
        position: fixed;
        top: 0;
        left: 0;
        background: rgb(0, 0, 0);
        color: #fff;
        width: 100%;
        height: 100%;
        z-index: 99999;
        display: flex;
        justify-content: center;
        align-items: center;
        animation: slide-fade 1s ease-in-out forwards;
        animation-delay: 1.3s;
    }
    
    .preloader-logo{
        
        font-size: 30px;
        animation: zoom .3s ease-in-out forwards;
        transform: scale(1);
        position: relative;
        overflow: hidden;
    }

    @keyframes zoom{
        0%{
            transform: scale(1);
        }
        100%{
            transform: scale(1.2);
        }
    }
    
    @keyframes slideRight{
        0%{
            transform: translate(-110%);
        }
        50%{
            transform: translate(0%);
        }
        100%{
            transform: translate(110%);
        }
    }

    @keyframes slide-fade{
        0%{
            transform: translateX(0%);
        }
        100%{
            transform: translateX(100%);
        }
    }

    .preloader-logo::before{
        position:absolute;
        content: "";
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: #2256EF;
        transform: translate(-110%);
        animation: slideRight 1s ease-in-out forwards;
        animation-delay: .3s;
    }
    
</style>