import TheMainTitle from './components/TheMainTitle'
import TheBeats from './components/TheBeats'
import TheSongs from './components/TheSongs'
import ThePolicy from './components/ThePolicy'
import TheTermsOfService from './components/TheTermsOfService'
import TheContact from './components/TheContact'
import ThePricing from './components/ThePricing'


const routes = [
    {
        path: '/',
        component: TheMainTitle,
    },

    {
        path: '/Beats',
        component: TheBeats,
    },

    {
        path: '/Songs',
        component: TheSongs,
    },

    {
        path: '/Policy',
        component: ThePolicy,
    },

    {
        path: '/TermsOfService',
        component: TheTermsOfService,
    },

    {
        path: '/Contact',
        component: TheContact,
    },

    {
        path: '/Pricing',
        component: ThePricing,
    }




]

export default routes;